* {
    box-sizing: border-box;
}
body {
    background: #15143c;
    padding: 0;
    margin: 0;
}

canvas {
    /*max-width: 400px;*/
    max-height: 100vh;
    max-width: 80%;
    float:left;
    border: 3px solid #413eb7;
}

.slider {
    color: white;
    padding: 10px;
    background: #15143c;
}
.slider:nth-child(2n+1) {
    background: #1f1e59;
}

.slider label {
    display: block;
    white-space: nowrap;
}
#controls {
    float:left;
    width: 19%;
    color: white;
}
#controls button, #fpscounter {
    margin: 10px 10px;
    padding: 5px;
}



/* range */
input[type=range] {
    -webkit-appearance: none;
    width: 100%;
    margin: 5.8px 0;
    background-color: transparent;
}
input[type=range]:focus {
    outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 4.4px;
    cursor: pointer;
    box-shadow: 2.4px 2.4px 1.4px #d5a2ff, 0px 0px 2.4px #e1bcff;
    background: #ac2fff;
    border-radius: 0px;
    border: 0px solid #d5a2ff;
}
input[type=range]::-webkit-slider-thumb {
    box-shadow: 1px 1px 3.9px rgba(255, 0, 0, 0.49), 0px 0px 1px rgba(255, 26, 26, 0.49);
    border: 1.6px solid #fdaedc;
    height: 16px;
    width: 4px;
    border-radius: 9px;
    background: rgba(253, 89, 189, 0.93);
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -5.8px;
}
input[type=range]:focus::-webkit-slider-runnable-track {
    background: #b649ff;
}
input[type=range]::-moz-range-track {
    width: 100%;
    height: 4.4px;
    cursor: pointer;
    box-shadow: 2.4px 2.4px 1.4px #d5a2ff, 0px 0px 2.4px #e1bcff;
    background: #ac2fff;
    border-radius: 0px;
    border: 0px solid #d5a2ff;
}
input[type=range]::-moz-range-thumb {
    box-shadow: 1px 1px 3.9px rgba(255, 0, 0, 0.49), 0px 0px 1px rgba(255, 26, 26, 0.49);
    border: 1.6px solid #fdaedc;
    height: 16px;
    width: 4px;
    border-radius: 9px;
    background: rgba(253, 89, 189, 0.93);
    cursor: pointer;
}
input[type=range]::-ms-track {
    width: 100%;
    height: 4.4px;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    color: transparent;
}
input[type=range]::-ms-fill-lower {
    background: #a216ff;
    border: 0px solid #d5a2ff;
    border-radius: 0px;
    box-shadow: 2.4px 2.4px 1.4px #d5a2ff, 0px 0px 2.4px #e1bcff;
}
input[type=range]::-ms-fill-upper {
    background: #ac2fff;
    border: 0px solid #d5a2ff;
    border-radius: 0px;
    box-shadow: 2.4px 2.4px 1.4px #d5a2ff, 0px 0px 2.4px #e1bcff;
}
input[type=range]::-ms-thumb {
    box-shadow: 1px 1px 3.9px rgba(255, 0, 0, 0.49), 0px 0px 1px rgba(255, 26, 26, 0.49);
    border: 1.6px solid #fdaedc;
    height: 16px;
    width: 4px;
    border-radius: 9px;
    background: rgba(253, 89, 189, 0.93);
    cursor: pointer;
    height: 4.4px;
}
input[type=range]:focus::-ms-fill-lower {
    background: #ac2fff;
}
input[type=range]:focus::-ms-fill-upper {
    background: #b649ff;
}
